}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  display: block;
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
video {
  object-fit: fill;
  max-width: 100%;
}

.container {
  margin: 0 auto;
